import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { DesignationBasedSettingsModel } from '@tei/material-smart-components';
import { firstValueFrom, Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { GetReservationDetailsSettingsRequest, GetReservationDetailsSettingsResponse } from '../../../@intermediate/web-settings/models/get-reservation-details-settings.model';
import { PostReservationDetailsSettingsRequest, ReservationDetailsSettingsForm } from '../../../@intermediate/web-settings/models/post-reservation-details-settings.model';
import { IWebSettingsService } from '../../../@intermediate/web-settings/web-settings.interface';

@Component({
  selector: 'tei-tee-sheet-administration-web-settings-reservation-details-settings',
  templateUrl: './reservation-details-settings.component.html',
  styleUrls: ['./reservation-details-settings.component.scss']
})
export class ReservationDetailsSettingsComponent implements OnInit
{
  form: FormGroup<ReservationDetailsSettingsForm>;
  model: GetReservationDetailsSettingsResponse;
  settings: DesignationBasedSettingsModel = {};
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_TIME_WEB_BOOKING_SETUP'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.NAVIGATION.RESERVATION_DETAILS'),
      relativeUrl: ''
    }
  ];
  reservationDetailsSettings$: Observable<GetReservationDetailsSettingsResponse>;

  constructor(
    @Inject('IWebSettingsService') private webSettingsService: IWebSettingsService,
    private translate: TranslateService,
    private snackbarService: SnackBarService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  )
  {
  }

  ngOnInit(): void
  {
    const request: GetReservationDetailsSettingsRequest = {
    };

    this.reservationDetailsSettings$ = this.webSettingsService.GetReservationDetailsSettings(request).pipe(
      tap((response) =>
      {
        this.model = response;
        this.settings = response.Settings;
        this.form = new FormGroup<ReservationDetailsSettingsForm>({
          Settings: new FormGroup({})
        });
      })
    );
  }

  async save(): Promise<void>
  {
    try
    {
      const request: PostReservationDetailsSettingsRequest = {
        PermissionTable: this.form.value.Settings
      };

      await firstValueFrom(this.webSettingsService.PostReservationDetailsSettings(request));

      this.snackbarService.showSuccess(this.translate.instant('TSADMINISTRATION.GENERAL.SUCCESS_MESSAGE'), '', null, 'left');

    } catch (error)
    {
    }
  }

  back(): void
  {
    this.router.navigate(['../'], { relativeTo: this.activatedRoute });
  }
}
