import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { DesignationBasedSettingsModel } from '@tei/material-smart-components';
import { firstValueFrom, Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { GetDisplaySettingsRequest, GetDisplaySettingsResponse } from '../../../@intermediate/web-settings/models/get-display-settings.model';
import { DisplaySettingsForm, PostDisplaySettingsRequest } from '../../../@intermediate/web-settings/models/post-display-settings.model';
import { IWebSettingsService } from '../../../@intermediate/web-settings/web-settings.interface';

@Component({
  selector: 'tei-tee-sheet-administration-web-settings-display-settings',
  templateUrl: './display-settings.component.html',
  styleUrls: ['./display-settings.component.scss']
})
export class DisplaySettingsComponent implements OnInit
{
  form: FormGroup<DisplaySettingsForm>;
  settings: DesignationBasedSettingsModel = {};
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_TIME_WEB_BOOKING_SETUP'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.NAVIGATION.TEE_TIME_DISPLAY'),
      relativeUrl: ''
    }
  ];
  displaySettings$: Observable<GetDisplaySettingsResponse>;

  constructor(
    @Inject('IWebSettingsService') private webSettingsService: IWebSettingsService,
    private translate: TranslateService,
    private snackbarService: SnackBarService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  )
  {
  }

  ngOnInit(): void
  {
    const request: GetDisplaySettingsRequest = {
    };

    this.displaySettings$ = this.webSettingsService.GetDisplaySettings(request).pipe(
      tap((response) =>
      {
        this.settings = response.Settings;
        this.form = new FormGroup<DisplaySettingsForm>({
          Settings: new FormGroup({})
        });
      })
    );
  }

  async save(): Promise<void>
  {
    try
    {
      const request: PostDisplaySettingsRequest = {
        PermissionTable: this.form.value.Settings
      };

      await firstValueFrom(this.webSettingsService.PostDisplaySettings(request));

      this.snackbarService.showSuccess(this.translate.instant('TSADMINISTRATION.GENERAL.SUCCESS_MESSAGE'), '', null, 'left');

    } catch (error)
    {

    }
  }

  back(): void
  {
    this.router.navigate(['../'], { relativeTo: this.activatedRoute });
  }
}
