import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ConfirmationSettingsComponent } from './confirmation-setting/confirmation-settings.component';
import { DisplaySettingsComponent } from './display-settings/display-settings.component';
import { ReservationDetailsSettingsComponent } from './reservation-details-setting/reservation-details-settings.component';
import { SearchAndFilteringComponent } from './search-and-filtering/search-and-filtering.component';
import { TermsAndConditionsComponent } from './terms-and-conditions/terms-and-conditions.component';
import { WebSettingsSettingComponent } from './web-settings-setting.component';

const routes: Routes = [
  { path: 'search-filtering', component: SearchAndFilteringComponent },
  { path: 'tee-time-display', component: DisplaySettingsComponent },
  { path: 'confirmation-dialog', component: ConfirmationSettingsComponent },
  { path: 'reservation-details', component: ReservationDetailsSettingsComponent },
  { path: 'terms-conditions', component: TermsAndConditionsComponent },
  { path: '', component: WebSettingsSettingComponent },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class WebSettingsRoutingModule
{
}
