import { Component, Inject, OnInit } from '@angular/core';
import { AbstractControl, FormControl, FormGroup, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { DesignationBasedSettingsModel } from '@tei/material-smart-components';
import moment from 'moment';
import { firstValueFrom, Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { DefaultHoleEnum, DefaultPlayersNumberEnum, GetSearchAndFilteringRequest, GetSearchAndFilteringResponse, TimeFormatEnum } from '../../../@intermediate/web-settings/models/get-search-and-filtering.model';
import { PostSearchAndFilteringRequest, SearchAndFilteringForm } from '../../../@intermediate/web-settings/models/post-search-and-filtering.model';
import { IWebSettingsService } from '../../../@intermediate/web-settings/web-settings.interface';
@Component({
  selector: 'tei-tee-sheet-administration-web-settings-search-and-filtering',
  templateUrl: './search-and-filtering.component.html',
  styleUrls: ['./search-and-filtering.component.scss']
})
export class SearchAndFilteringComponent implements OnInit
{
  form: FormGroup<SearchAndFilteringForm>;
  settings: DesignationBasedSettingsModel = {};
  model: GetSearchAndFilteringResponse;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_TIME_WEB_BOOKING_SETUP'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.NAVIGATION.SEARCH_AMP_FILTERING'),
      relativeUrl: ''
    }
  ];
  searchAndFiltering$: Observable<GetSearchAndFilteringResponse>;
  DefaultPlayersNumberEnum = DefaultPlayersNumberEnum;
  DefaultHoleEnum = DefaultHoleEnum;
  TimeFormatEnum = TimeFormatEnum;
  startTime: Date = new Date(1995, 11, 17, 0, 0);
  endTime: Date = new Date(1995, 11, 17, 23, 45);

  constructor(
    @Inject('IWebSettingsService') private webSettingsService: IWebSettingsService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private responsiveService: ResponsiveService,
    private snackbarService: SnackBarService,
    private translate: TranslateService
  )
  {
  }

  ngOnInit(): void
  {
    const request: GetSearchAndFilteringRequest = {
    };

    this.searchAndFiltering$ = this.webSettingsService.GetSearchAndFiltering(request).pipe(
      tap((response) =>
      {
        this.model = response;
        this.form = new FormGroup<SearchAndFilteringForm>({
          UseNewTeeTimeSettings: new FormControl<boolean>({ value: response?.UseNewTeeTimeSettings, disabled: !this.model?.IsSuperUser }),
          AllowAllCourses: new FormControl<boolean>(response?.AllowAllCourses),
          MaxCourseSelection: new FormControl<number>(response?.MaxCourseSelection, Validators.pattern(/^\d+$/)),
          DefaultPlayersNumber: new FormControl<DefaultPlayersNumberEnum>(response?.DefaultPlayersNumber),
          MaxPlayerSelection: new FormControl<number>(response?.MaxPlayerSelection ?? 4),
          MaxTeeTimesShown: new FormControl<number>(response?.MaxTeeTimesShown ?? null,[Validators.required, Validators.min(1), Validators.max(100), Validators.pattern(/^\d+$/)]),
          DefaultHole: new FormControl<DefaultHoleEnum>(response?.DefaultHole),
          HoldTime: new FormControl<number>(response?.HoldTime, Validators.pattern(/^\d+$/)),
          TimeFormat: new FormControl<TimeFormatEnum>(response?.TimeFormat),
          Interval: new FormControl<number>(response?.Interval ?? null, [Validators.required, Validators.min(1), Validators.pattern(/^\d+$/)]),
          StartTime: new FormControl<Date>(moment(response?.StartTime ?? "00:00", "HH:mm").toDate()),
          EndTime: new FormControl<Date>(moment(response?.EndTime ?? "00:00", "HH:mm").toDate()),
          Settings: new FormGroup({})
        }, { validators: this.startTimeBeforeEndTimeValidator });
        this.settings = response.Settings;
      })
    );
  }

  startTimeBeforeEndTimeValidator: ValidatorFn = (group: AbstractControl): ValidationErrors | null =>
  {
    const startTimeControl = group.get('StartTime');
    const endTimeControl = group.get('EndTime');

    const startTime = startTimeControl?.value;
    const endTime = endTimeControl?.value;

    if (startTime && endTime && startTime > endTime)
    {
      startTimeControl?.setErrors({ startTimeAfterEndTime: true });
      endTimeControl?.setErrors({ startTimeAfterEndTime: true });
    } else
    {
      startTimeControl?.setErrors(null);
      endTimeControl?.setErrors(null);
    }

    return null;
  };

  async save(): Promise<void>
  {
    try
    {
      const request: PostSearchAndFilteringRequest = {
        UseNewTeeTimeSettings: this.form.value.UseNewTeeTimeSettings,
        AllowAllCourses: this.form.value.AllowAllCourses,
        MaxCourseSelection: this.form.value.MaxCourseSelection,
        DefaultPlayersNumber: this.form.value.DefaultPlayersNumber,
        MaxPlayerSelection: this.form.value.MaxPlayerSelection,
        MaxTeeTimesShown: this.form.value.MaxTeeTimesShown,
        DefaultHole: this.form.value.DefaultHole,
        HoldTime: this.form.value.HoldTime,
        TimeFormat: this.form.value.TimeFormat,
        Interval: this.form.value.Interval,
        StartTime: moment(this.form.value.StartTime).format("HH:mm"),
        EndTime: moment(this.form.value.EndTime).format("HH:mm"),
        PermissionTable: this.form.value.Settings as DesignationBasedSettingsModel
      };

      await firstValueFrom(this.webSettingsService.PostSearchAndFiltering(request));

      this.snackbarService.showSuccess(this.translate.instant('TSADMINISTRATION.GENERAL.SUCCESS_MESSAGE'), '', null, 'left');

    } catch (error)
    {

    }
  }

  back(): void
  {
    this.router.navigate(['../'], { relativeTo: this.activatedRoute });
  }
}
