import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { firstValueFrom, Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { GetTermsAndConditionsRequest, GetTermsAndConditionsResponse } from '../../../@intermediate/web-settings/models/get-terms-and-conditions.model';
import { PostTermsAndConditionsRequest, TermsAndConditionsForm } from '../../../@intermediate/web-settings/models/post-terms-and-conditions.model';
import { IWebSettingsService } from '../../../@intermediate/web-settings/web-settings.interface';

@Component({
  selector: 'tei-tee-sheet-administration-web-settings-terms-and-conditions',
  templateUrl: './terms-and-conditions.component.html',
  styleUrls: ['./terms-and-conditions.component.scss']
})
export class TermsAndConditionsComponent implements OnInit
{
  form: FormGroup<TermsAndConditionsForm>;
  model: GetTermsAndConditionsResponse;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_TIME_WEB_BOOKING_SETUP'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.WEB_SETTINGS.NAVIGATION.TERMS_AMP_CONDITIONS'),
      relativeUrl: ''
    }
  ];
  termsAndConditions$: Observable<GetTermsAndConditionsResponse>;

  constructor(
    @Inject('IWebSettingsService') private webSettingsService: IWebSettingsService,
    private translate: TranslateService,
    private snackbarService: SnackBarService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  )
  {
  }

  ngOnInit(): void
  {
    const request: GetTermsAndConditionsRequest = {
    };

    this.termsAndConditions$ = this.webSettingsService.GetTermsAndConditions(request).pipe(
      tap((response) =>
      {
        this.model = response;
        this.form = new FormGroup<TermsAndConditionsForm>({
          EnableTermsConditions: new FormControl<boolean>(response?.EnableTermsConditions),
          TermsConditions: new FormControl<string>(response?.TermsConditions),
        });
      })
    );
  }

  async save(): Promise<void>
  {

    try
    {
      const request: PostTermsAndConditionsRequest = {
        EnableTermsConditions: this.form.value.EnableTermsConditions,
        TermsConditions: this.form.value.TermsConditions
      };

      await firstValueFrom(this.webSettingsService.PostTermsAndConditions(request));
      this.snackbarService.showSuccess(this.translate.instant('TSADMINISTRATION.GENERAL.SUCCESS_MESSAGE'), '', null, 'left');
    } catch (error)
    {

    }
  }

  back(): void
  {
    this.router.navigate(['../'], { relativeTo: this.activatedRoute });
  }
}
