import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable, of } from "rxjs";
import { GetConfirmationSettingsRequest, GetConfirmationSettingsResponse } from "../@intermediate/web-settings/models/get-confirmation-settings.model";
import { GetDisplaySettingsRequest, GetDisplaySettingsResponse } from "../@intermediate/web-settings/models/get-display-settings.model";
import { GetReservationDetailsSettingsRequest, GetReservationDetailsSettingsResponse } from "../@intermediate/web-settings/models/get-reservation-details-settings.model";
import { GetSearchAndFilteringRequest, GetSearchAndFilteringResponse } from "../@intermediate/web-settings/models/get-search-and-filtering.model";
import { GetTermsAndConditionsRequest, GetTermsAndConditionsResponse } from "../@intermediate/web-settings/models/get-terms-and-conditions.model";
import { PostDisplaySettingsRequest, PostDisplaySettingsResponse } from "../@intermediate/web-settings/models/post-display-settings.model";
import { PostReservationDetailsSettingsRequest, PostReservationDetailsSettingsResponse } from "../@intermediate/web-settings/models/post-reservation-details-settings.model";
import { PostSearchAndFilteringRequest, PostSearchAndFilteringResponse } from "../@intermediate/web-settings/models/post-search-and-filtering.model";
import { PostTermsAndConditionsRequest, PostTermsAndConditionsResponse } from "../@intermediate/web-settings/models/post-terms-and-conditions.model";
import { IWebSettingsService } from "../@intermediate/web-settings/web-settings.interface";
import { PostConfirmationSettingsRequest, PostConfirmationSettingsResponse } from "../@intermediate/web-settings/models/post-confirmation-settings.model";

@Injectable()
export class WebSettingsService implements IWebSettingsService
{
  private readonly serviceUrl: string;

  constructor(@Inject("IAPIBaseUrlProvider") protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/TShAdministration/WebSettings";
  }

  PostSearchAndFiltering(request: PostSearchAndFilteringRequest): Observable<PostSearchAndFilteringResponse>
  {
    const apiUrl = this.serviceUrl + "/SearchFiltering";

    return this.http.post<PostSearchAndFilteringResponse>(apiUrl, request);
  }

  GetSearchAndFiltering(request: GetSearchAndFilteringRequest): Observable<GetSearchAndFilteringResponse>
  {
    const apiUrl = this.serviceUrl + "/SearchFiltering";

    return this.http.get<GetSearchAndFilteringResponse>(apiUrl, { params: request as any });
  }

  PostDisplaySettings(request: PostDisplaySettingsRequest): Observable<PostDisplaySettingsResponse>
  {
    const apiUrl = this.serviceUrl + "/DisplaySettings";

    return this.http.post<PostDisplaySettingsResponse>(apiUrl, request);
  }

  GetDisplaySettings(request: GetDisplaySettingsRequest): Observable<GetDisplaySettingsResponse>
  {
    const apiUrl = this.serviceUrl + "/DisplaySettings";

    return this.http.get<GetDisplaySettingsResponse>(apiUrl, { params: request as any });
  }

  GetConfirmationSettings(request: GetConfirmationSettingsRequest): Observable<GetConfirmationSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "/ConfirmationSettings";
    return this.http.get<GetConfirmationSettingsResponse>(apiUrl, { params: request as any });
  }

  PostConfirmationSettings(request: PostConfirmationSettingsRequest): Observable<PostConfirmationSettingsResponse> {
    const apiUrl = this.serviceUrl + "/ConfirmationSettings";
    return this.http.post<PostConfirmationSettingsResponse>(apiUrl, request);
  }

  PostReservationDetailsSettings(request: PostReservationDetailsSettingsRequest): Observable<PostReservationDetailsSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "/ReservationDetailsSettings";
    return this.http.post<PostReservationDetailsSettingsRequest>(apiUrl, request);
  }

  GetReservationDetailsSettings(request: GetReservationDetailsSettingsRequest): Observable<GetReservationDetailsSettingsResponse>
  {
    const apiUrl = this.serviceUrl + "/ReservationDetailsSettings";

    return this.http.get<GetReservationDetailsSettingsResponse>(apiUrl, { params: request as any });
  }

  GetTermsAndConditions(request: GetTermsAndConditionsRequest): Observable<GetTermsAndConditionsResponse>
  {
    const apiUrl = this.serviceUrl + "/TermsAndConditionsSettings";
    return this.http.get<GetTermsAndConditionsResponse>(apiUrl, { params: request as any });
  }

  PostTermsAndConditions(request: PostTermsAndConditionsRequest): Observable<PostTermsAndConditionsResponse>
  {
    const apiUrl = this.serviceUrl + "/TermsAndConditionsSettings";
    return this.http.post<GetTermsAndConditionsResponse>(apiUrl, request);
  }
}

